import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { IToast, Message } from './toast.model';

@Injectable({
  providedIn: 'root'
})
export class NgToastService {

  private toastDetails$ = new Subject<IToast>();

  constructor() { }

   getToastDetails(){
    return this.toastDetails$.asObservable();
  }

  success(message:IToast){
    this.toastDetails$.next(<IToast>{type:Message.success,detail:message.detail,position:message.position, summary:message.summary,duration:message.duration,sticky:message.sticky})
  }
  error(message:IToast){
    this.toastDetails$.next(<IToast>{ type: Message.error,detail:message.detail,position:message.position,summary:message.summary,duration:message.duration,sticky:message.sticky})
  }
  info(message:IToast){
    this.toastDetails$.next(<IToast>{type:Message.info,detail:message.detail,position:message.position,summary:message.summary,duration:message.duration,sticky:message.sticky})
  }
  warning(message:IToast){
    this.toastDetails$.next(<IToast>{type:Message.warning,detail:message.detail,summary:message.summary,position:message.position,duration:message.duration,sticky:message.sticky})
  }
}
